import {
  GET_CONTACTS,
  DELETE_CONTACT,
  ADD_CONTACT,
  GET_CONTACT,
  UPDATE_CONTACT,
  SORT_CONTACTS_ASC,
  SORT_CONTACTS_DESC
} from '../actions/types';

const initialState = {
  contacts: [],
  contact: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_CONTACTS:
      return {
        ...state,
        contacts: action.payload
      };
    case DELETE_CONTACT:
      return {
        ...state,
        contacts: state.contacts.filter(
          contact => contact.id !== action.payload
        )
      };
    case ADD_CONTACT:
      return {
        ...state,
        contacts: [action.payload, ...state.contacts]
      };
    case GET_CONTACT:
      return {
        ...state,
        contact: action.payload
      };
    case UPDATE_CONTACT:
      return {
        ...state,
        contacts: state.contacts.map(
          contact =>
            contact.id === action.payload.id
              ? (contact = action.payload)
              : contact
        )
      };
    case SORT_CONTACTS_ASC:
      return {
        ...state,
        contacts: [
          ...state.contacts.sort((a, b) => {
            return a.name > b.name ? 1 : b.name > a.name ? -1 : 0;
          })
        ]
      };
    case SORT_CONTACTS_DESC:
      return {
        ...state,
        contacts: [
          ...state.contacts.sort((a, b) => {
            return a.name < b.name ? 1 : b.name < a.name ? -1 : 0;
          })
        ]
      };
    default:
      return state;
  }
}



